	import { bindCardAndPay ,isNeedPay, getCarListSecond, getCarList, getYanZhengCode, addCarCard, addCarCardAndBindUser } from 'common/js/requestUrl'
	import {request,showToast} from 'common/js/common'
	
	export default {
		data() {
			return {
				yanzhengStart: false,
				yanzhengTime: 0,
				yanzhengCode: null,
				code: 0,
				showBack: false,//遮罩层
				showCarMsg: false,
				showCarSecondMsg: false, 
				cards: '',
				prefix: '',
				bindPrefix: '浙', //绑定的时候的前缀
				bindCard: '',
				phone: '',
				fName: '',
				Timer: null,//定时器
				address_code: '',
				id: null,
				type: 1,
				needBindUser: true,
				messageIsShow: false,
				choosedSex: 1,//1->男 2->女
				placeList:["京", "津", "沪","渝","冀","吉","辽", "黑","湘","鄂","甘","晋","陕","豫","川", "云", "桂","蒙","贵", "青","藏","新", "宁", "粤","琼","闽","苏","浙","赣","鲁", "皖"],
				yzFocus: false,
				carList: [],
				carListSecond: [],
				xinghao: '选填',
				car_id: 0,
				needPay: false, //是否需要支付激活费用
			};
		},
		onLoad(query){
			this.type = query.type
			this.id = query.id
			// if(this.id){
			// 	request(isNeedPay, {card_id: this.id}, (res) => {
			// 		let needPay = res.result
			// 		if(needPay == 1){
			// 			this.needPay = true
			// 		}else{
			// 			this.needPay = false
			// 		}
			// 	})
			// }
		},
		onShow(){
			this.getCarList()
		},
		computed:{
			showLight(){
				return this.bindCard.length == 7 ? '/static/img/icon/light.png' : '/static/img/icon/unlight.png'
			},
			leftSexStatus(){
				return this.choosedSex == 1 ? 'name-choose-left' : 'name-choose-right'
			},
			rightSexStatus(){
				return this.choosedSex == 2 ? 'name-choose-left' : 'name-choose-right'
			}
		},
		methods: {
			changeSex(sex){
				this.choosedSex = sex
			},
			showModel(){
				this.showBack = true
			},
			cancleBack(){
				this.showBack = false
			},
			chooseItem(item){
				this.bindPrefix = item
				this.showBack = false
			},
			setCard(e){
				var patrn=/[\u4E00-\u9FA5]|[\uFE30-\uFFA0]/gi; 
				if(patrn.exec(e.detail.value)){ 
					showToast('车牌号中不应该包含中文')
				}
				this.bindCard = e.detail.value.toUpperCase()
			},
			getPhoneNumber(e) {  
                console.log(e)
            },  
			getCode(){
				if(!this.yanzhengStart){
					if(!this.phone){
						showToast('您还未输入手机号')
						return
					}
					request(getYanZhengCode,{
						phone: this.phone
					},(res) => {
						if(res.code == 200){
							this.yanzhengStart = true
							this.yanzhengTime = 30
							this.Timer = setInterval(this.timeGone , 1000)
							this.code = res.result
							this.yzFocus = true
						}
					})
				}
			},
			getCarList(){
				request(getCarList,{},(res) => {
					if(res.code == 200){
						this.carList = res.result
					}
				})
			},
			changeSecondCar(parentid){
				request(getCarListSecond,{
					parentid
				},(res) => {
					if(res.code == 200){
						this.carListSecond = res.result
						this.showCarSecondMsg = true
					}
				})
			},
			timeGone(){
				this.yanzhengTime -= 1
				if(this.yanzhengTime == 0){
					this.yanzhengStart = false
					clearInterval(this.Timer)
				}
			},
			inputPhone(e){
				this.phone = e.detail.value
			},
			inputFName(e){
				this.fName = e.detail.value
			},
			inputYanzhengCode(e){
				this.yanzhengCode = e.detail.value
			},
			bindCardAndPayMoney(){
				if(!this.bindPrefix){
					showToast('您还未选择车牌前缀')
					return
				}
				if(!this.bindCard){
					showToast('您还未填写车牌')
					return
				}
				var patrn=/[\u4E00-\u9FA5]|[\uFE30-\uFFA0]/gi; 
				if(patrn.exec(this.bindCard)){ 
					showToast('车牌号中不应该包含中文')
					return
				}
				if(!this.phone){
					showToast('您还未输入手机号')
					return
				}
				if(!this.yanzhengCode){
					showToast('您还未填写验证码')
					return
				}
				if(!this.fName){
					showToast('您还填写车主称呼')
					return
				}
				if(this.yanzhengCode != this.code){
					showToast('验证码错误！')
					return
				}
				this.bindCardAndPay()
			},
			bindCardConfirm(){
				if(!this.bindPrefix){
					showToast('您还未选择车牌前缀')
					return
				}
				if(!this.bindCard){
					showToast('您还未填写车牌')
					return
				}
				var patrn=/[\u4E00-\u9FA5]|[\uFE30-\uFFA0]/gi; 
				if(patrn.exec(this.bindCard)){ 
					showToast('车牌号中不应该包含中文')
					return
				}
				if(!this.phone){
					showToast('您还未输入手机号')
					return
				}
				if(!this.yanzhengCode){
					showToast('您还未填写验证码')
					return
				}
				if(!this.fName){
					showToast('您还填写车主称呼')
					return
				}
				if(this.yanzhengCode != this.code){
					showToast('验证码错误！')
					return
				}
				if(this.type == 2){
					this.addCarCardAndBind()
				}else{
					this.addCard()
				}
			},
			bindCardAndPay(){
				request(bindCardAndPay,{
					card: this.bindCard,
					phone: this.phone,
					prefix: this.bindPrefix,
					fName: this.fName,
					sex: this.choosedSex,
					card_id: this.id,
					car_id: this.car_id
				},(res) =>{
					if(res.code == 200){
						uni.requestPayment({
							provider: 'wxpay',
							timeStamp: res.result.timeStamp,
							nonceStr: res.result.nonceStr,
							package: res.result.package,
							signType: res.result.signType,
							paySign: res.result.paySign,
							success: function (res) {
								showToast('支付成功！')
								setTimeout(function(){
									uni.switchTab({
										url: '../tabbar-1/tabbar-1?showCoupon=1'
									})	
								}, 1000)
							},
							fail: function (err) {
								showToast('您未支付，无法激活')
							}
						})
					}
				})
			},
			addCarCardAndBind(){
				request(addCarCardAndBindUser,{
					card: this.bindCard,
					phone: this.phone,
					prefix: this.bindPrefix,
					fName: this.fName,
					sex: this.choosedSex,
					card_id: this.id,
					car_id: this.car_id
				},(res) =>{
					if(res.code == 200){
						showToast('绑定成功','success')
						//跳转到成功界面
						uni.switchTab({
							url: '../tabbar-1/tabbar-1?showCoupon=1'
						});	
					}else if(res.code == 900){
						// 自己已经绑定，需要处理
						uni.showModal({
							title: '提示',
							content: '您已经绑定了挪车码，如果还要继续绑定此挪车码需要先去个人中心取消绑定，是否前去个人中心',
							success: function (res) {
								if (res.confirm) {
									uni.switchTab({
										url: '../tabbar-5/tabbar-5'
									});	
								} else if (res.cancel) {
									
								}
							}
						});
					}
				})
			},
			addCard(){
				request(addCarCard,{
					card: this.bindCard,
					phone: this.phone,
					prefix: this.bindPrefix,
					fName: this.fName,
					sex: this.choosedSex,
					car_id: this.car_id
				},(res) => {
					if(res.code == 200){
						showToast('添加成功','success')
						uni.navigateBack({
							delta: 1
						});
					}
				})
			},
			chooseCar(id, name){
				this.car_id = id
				this.xinghao = name
				this.showCarMsg = false
			},
			changeShowCarMsg(){
				this.showCarMsg = true
			}
		}
	}
